.skills__grid{display:grid;grid-template-columns:repeat(auto-fit, minmax(280px, 1fr));gap:2rem;margin-top:2rem}.skills__category{background:rgba(37,99,235,.05);padding:1.5rem;border-radius:8px;height:100%}.skills__category h3{color:#2563eb;margin-bottom:1.5rem;font-size:1.2rem;font-weight:600;border-bottom:2px solid rgba(37,99,235,.2);padding-bottom:.5rem}.skills__category ul{list-style:none}.skills__category ul li{margin-bottom:.5rem;padding-left:1.5rem;position:relative;line-height:1.4}.skills__category ul li::before{content:"•";color:#2563eb;font-weight:bold;position:absolute;left:0}.dark-mode .skills__category{background:#1e293b;border:1px solid rgba(241,245,249,.1)}.dark-mode .skills__category h3{color:#60a5fa;border-bottom:2px solid rgba(96,165,250,.3)}.dark-mode .skills__category ul li{color:#f1f5f9}.dark-mode .skills__category ul li::before{color:#60a5fa}